'use client';

import React, { useState } from 'react';
import {
  Box,
  Drawer,
  AppBar,
  Toolbar,
  List,
  Typography,
  Divider,
  IconButton,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Collapse,
  Avatar,
  Menu,
  MenuItem,
  Breadcrumbs,
  Link,
  Select,
  FormControl,
  InputLabel,
} from '@mui/material';
import {
  Menu as MenuIcon,
  Dashboard,
  People,
  Business,
  Security,
  Person,
  KeyboardArrowDown,
  KeyboardArrowRight,
  AccountCircle,
  Home,
  AdminPanelSettings,
  SwapHoriz,
  VpnKey,
  Map,
  Settings,
  Assignment,
} from '@mui/icons-material';
import { usePathname, useRouter } from 'next/navigation';
import { useAppContext } from '@/app/layout';

const drawerWidth = 280;

interface MenuItemType {
  id: string;
  text: string;
  icon: React.ReactElement;
  children?: MenuItemType[];
  path?: string;
}

const menuItems: MenuItemType[] = [
  {
    id: 'dashboard',
    text: 'Dashboard',
    icon: <Dashboard />,
    path: '/dashboard',
  },
  {
    id: 'user-management',
    text: 'User Management',
    icon: <People />,
    children: [
      {
        id: 'user-directory',
        text: 'User Directory',
        icon: <People />,
        path: '/user-directory',
      },
      {
        id: 'map-user-roles',
        text: 'Map User Roles',
        icon: <Assignment />,
        path: '/map-user-roles',
      },
      {
        id: 'dsc-registration',
        text: 'DSC Registration',
        icon: <VpnKey />,
        path: '/dsc-registration',
      },
    ],
  },
  {
    id: 'organization',
    text: 'Organization',
    icon: <Business />,
    children: [
      {
        id: 'map-ulb-district',
        text: 'Map ULB to District',
        icon: <Map />,
        path: '/map-ulb-district',
      },
      {
        id: 'transfer-employee',
        text: 'Transfer Employee',
        icon: <SwapHoriz />,
        path: '/transfer-employee',
      },
    ],
  },
  {
    id: 'access-control',
    text: 'Access Control',
    icon: <Security />,
    children: [
      {
        id: 'levels-to-roles',
        text: 'Levels to Roles',
        icon: <AdminPanelSettings />,
        path: '/levels-to-roles',
      },
      {
        id: 'menu-configuration',
        text: 'Menu Configuration',
        icon: <Settings />,
        path: '/menu-configuration',
      },
      {
        id: 'master-maintenance',
        text: 'Master Maintenance',
        icon: <Settings />,
        path: '/master-maintenance',
      },
    ],
  },
];

interface MainLayoutProps {
  children: React.ReactNode;
}

const MainLayout: React.FC<MainLayoutProps> = ({ children }) => {
  const [mobileOpen, setMobileOpen] = useState(false);
  const [openMenus, setOpenMenus] = useState<{ [key: string]: boolean }>({});
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  
  const pathname = usePathname();
  const router = useRouter();
  const { currentULB, setCurrentULB } = useAppContext();

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const handleMenuToggle = (menuId: string) => {
    setOpenMenus(prev => ({
      ...prev,
      [menuId]: !prev[menuId],
    }));
  };

  const handleProfileClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleProfileClose = () => {
    setAnchorEl(null);
  };

  const handleNavigation = (path: string) => {
    router.push(path);
  };

  const renderMenuItem = (item: MenuItemType, depth = 0) => {
    const isActive = pathname === item.path;
    const hasChildren = item.children && item.children.length > 0;

    if (hasChildren) {
      return (
        <React.Fragment key={item.id}>
          <ListItem disablePadding>
            <ListItemButton
              onClick={() => handleMenuToggle(item.id)}
              sx={{
                pl: 2 + depth * 2,
                backgroundColor: isActive ? 'primary.main' : 'transparent',
                color: isActive ? 'white' : 'inherit',
                '&:hover': {
                  backgroundColor: isActive ? 'primary.dark' : 'rgba(0, 0, 0, 0.04)',
                },
              }}
            >
              <ListItemIcon sx={{ color: isActive ? 'white' : 'inherit' }}>
                {item.icon}
              </ListItemIcon>
              <ListItemText primary={item.text} />
              {openMenus[item.id] ? <KeyboardArrowDown /> : <KeyboardArrowRight />}
            </ListItemButton>
          </ListItem>
          <Collapse in={openMenus[item.id]} timeout="auto" unmountOnExit>
            <List component="div" disablePadding>
              {item.children?.map((child) => renderMenuItem(child, depth + 1))}
            </List>
          </Collapse>
        </React.Fragment>
      );
    }

    return (
      <ListItem key={item.id} disablePadding>
        <ListItemButton
          onClick={() => item.path && handleNavigation(item.path)}
          sx={{
            pl: 2 + depth * 2,
            backgroundColor: isActive ? 'primary.main' : 'transparent',
            color: isActive ? 'white' : 'inherit',
            '&:hover': {
              backgroundColor: isActive ? 'primary.dark' : 'rgba(0, 0, 0, 0.04)',
            },
          }}
        >
          <ListItemIcon sx={{ color: isActive ? 'white' : 'inherit' }}>
            {item.icon}
          </ListItemIcon>
          <ListItemText primary={item.text} />
        </ListItemButton>
      </ListItem>
    );
  };

  const drawer = (
    <div>
      <Toolbar>
        <Typography variant="h6" noWrap component="div" sx={{ fontWeight: 'bold', color: 'primary.main' }}>
          KMDS ERP
        </Typography>
      </Toolbar>
      <Divider />
      <List>
        {menuItems.map((item) => renderMenuItem(item))}
      </List>
      <Divider />
      <List>
        <ListItem disablePadding>
          <ListItemButton onClick={() => handleNavigation('/profile')}>
            <ListItemIcon>
              <Person />
            </ListItemIcon>
            <ListItemText primary="My Profile" />
          </ListItemButton>
        </ListItem>
      </List>
    </div>
  );

  return (
    <Box sx={{ display: 'flex' }}>
      <AppBar
        position="fixed"
        sx={{
          width: { sm: `calc(100% - ${drawerWidth}px)` },
          ml: { sm: `${drawerWidth}px` },
          backgroundColor: 'white',
          color: 'black',
          boxShadow: '0px 2px 4px rgba(0,0,0,0.1)',
        }}
      >
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            sx={{ mr: 2, display: { sm: 'none' } }}
          >
            <MenuIcon />
          </IconButton>
          
          {/* Breadcrumbs */}
          <Breadcrumbs aria-label="breadcrumb" sx={{ flexGrow: 1 }}>
            <Link underline="hover" color="inherit" onClick={() => router.push('/')} sx={{ cursor: 'pointer' }}>
              <Home sx={{ mr: 0.5 }} fontSize="inherit" />
              Home
            </Link>
            <Typography color="text.primary">User Directory</Typography>
          </Breadcrumbs>

          {/* ULB Selector */}
          <FormControl size="small" sx={{ minWidth: 200, mr: 2 }}>
            <InputLabel>Current ULB</InputLabel>
            <Select
              value={currentULB}
              label="Current ULB"
              onChange={(e) => setCurrentULB(e.target.value)}
            >
              <MenuItem value="Nelamangala CMC">Nelamangala CMC</MenuItem>
              <MenuItem value="Hebbagodi CMC">Hebbagodi CMC</MenuItem>
            </Select>
          </FormControl>

          {/* Profile Menu */}
          <IconButton onClick={handleProfileClick} color="inherit">
            <Avatar sx={{ width: 32, height: 32, bgcolor: 'primary.main' }}>
              <AccountCircle />
            </Avatar>
          </IconButton>
          <Menu
            anchorEl={anchorEl}
            open={Boolean(anchorEl)}
            onClose={handleProfileClose}
            onClick={handleProfileClose}
          >
            <MenuItem onClick={() => router.push('/profile')}>
              <Person sx={{ mr: 1 }} />
              Profile
            </MenuItem>
            <MenuItem onClick={() => router.push('/change-password')}>
              <VpnKey sx={{ mr: 1 }} />
              Change Password
            </MenuItem>
            <Divider />
            <MenuItem onClick={() => console.log('Logout')}>
              Logout
            </MenuItem>
          </Menu>
        </Toolbar>
      </AppBar>

      <Box
        component="nav"
        sx={{ width: { sm: drawerWidth }, flexShrink: { sm: 0 } }}
        aria-label="mailbox folders"
      >
        <Drawer
          variant="temporary"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true,
          }}
          sx={{
            display: { xs: 'block', sm: 'none' },
            '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth },
          }}
        >
          {drawer}
        </Drawer>
        <Drawer
          variant="permanent"
          sx={{
            display: { xs: 'none', sm: 'block' },
            '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth },
          }}
          open
        >
          {drawer}
        </Drawer>
      </Box>

      <Box
        component="main"
        sx={{
          flexGrow: 1,
          p: 3,
          width: { sm: `calc(100% - ${drawerWidth}px)` },
          mt: 8,
          backgroundColor: 'background.default',
          minHeight: 'calc(100vh - 64px)',
        }}
      >
        {children}
      </Box>
    </Box>
  );
};

export default MainLayout;