'use client';

import React from 'react';
import MainLayout from '@/components/layout/MainLayout';
import { Box, Typography, Button } from '@mui/material';
import { useRouter } from 'next/navigation';

export default function HomePage() {
  const router = useRouter();

  return (
    <MainLayout>
      <Box sx={{ textAlign: 'center', mt: 8 }}>
        <Typography variant="h3" component="h1" gutterBottom sx={{ color: 'primary.main', fontWeight: 'bold' }}>
          Welcome to KMDS ERP System
        </Typography>
        <Typography variant="h6" color="text.secondary" sx={{ mb: 4 }}>
          Karnataka Municipal Data Society Enterprise Resource Planning
        </Typography>
        <Button 
          variant="contained" 
          size="large" 
          onClick={() => router.push('/user-directory')}
          sx={{ textTransform: 'none' }}
        >
          Go to User Directory
        </Button>
      </Box>
    </MainLayout>
  );
}